div#section0 div.inner img {
  display: none;
}

div#section0 div.inner ul {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 54px;
  left: 50%;
  transform: translateX(-50%);
}

div#section0 div.inner ul li {
  border: 1px solid white;
