  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

div#content01 div.inner div.swiper div.swiper-slide div.content {
  position: absolute;
  z-index: 150;
  width: 32%;
  text-align: left;
  color: white;
  top:50%;
  left:10%;
  transform:translateY(-50%);
}

div#content01 div.inner div.swiper div.swiper-slide div.content h4 {
  font-size: 24px;
  font-family:"nanumsquare-B";
  font-weight: normal;
  font-style: normal;
  margin-bottom: 30px;
