div#content01 div.inner div.swiper div.swiper-slide div.content p {
  font-size: 18px;
  font-family:"nanumsquare-R",sans-serif;
  font-weight: normal;
  font-style: normal;
  line-height: 31px;
  margin-bottom: 105px;
}

div#content01 div.inner div.swiper div.swiper-slide div.content button.btn {
  padding: 14px 50px;
  background: #f47500;
  border: none;
  font-size: 16px;
  font-weight: 400;
  font-family:"nanumsquare-R",sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #fff;
  cursor: pointer;
  border-radius: 40px;
}

