div.swiper-slide img.c {
  position: absolute;
  z-index: 1;
  width: 250px;
  height: 349px;
  top: 32%;
  right: 90px;
}

@keyframes spreadCards {
  0% {
    transform: rotate(0deg) translateY(0) translateX(0);
    opacity: 0;
  }

  100% {
    transform: rotate(calc((var(--index) - 2) * 15deg)) translateY(0px) translateX(calc((var(--index) - 2) * 15px));
    opacity: 1;
  }
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container {
  position: absolute;
  z-index: 200;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 300px;
  perspective: 1500px;
  right: 330px;
  top: 27%;
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card {
  --index: 0;
  opacity: 0;
  position: absolute;
  bottom: 0;
  transform-origin: bottom center;
  animation: spreadCards 0.3s ease-out forwards;
  animation-delay: calc(var(--index) * 0.2s);
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card:nth-child(1) {
  --index: 0;
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card:nth-child(2) {
  --index: 1;
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card:nth-child(3) {
  --index: 2;
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card:nth-child(4) {
  --index: 3;
}

div#content01 div.inner div.swiper div.swiper-slide div.card-container img.card:nth-child(5) {
  --index: 4;
}

