header.cd-auto-hide-header {
  width: 100%;
  height: 90px;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  background: rgba(31, 31, 31, 0.8);
  /* Force Hardware Acceleration */
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  will-change: transform;
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  transition: transform 0.3s ease-in-out;
}

header.cd-auto-hide-header::after {
  clear: both;
  content: "";
  display: block;
}

header.cd-auto-hide-header.is-hidden {
  -webkit-transform: translateY(-140%);
  -ms-transform: translateY(-140%);
  transform: translateY(-140%);
}

header.cd-auto-hide-header div.inner {
  width: 89.58%;
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

header.cd-auto-hide-header div.inner h1.pc-logo {
  position: absolute;
  top: 50%;
  bottom: auto;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

header div.inner h1 a {
  display: inline-block;
}

header div.inner h1 a img {
  width: 90%;
  height: 90%;
}

header div.inner h1.tm-logo {
  display: none;
}

header div.inner div.tm-right {
  display: none;
}

header.cd-auto-hide-header div.inner nav {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  padding-top: 36px;
}

header.cd-auto-hide-header div.inner nav ul {
  text-align: center;
}

header.cd-auto-hide-header div.inner nav ul li {
  display: inline-block;
}

header.cd-auto-hide-header div.inner nav ul li a {
  display: inline-block;
  padding: 0px 40px;
  color: #fff;
  font-size: 20px;
  font-family: 'Vitro_core';
  font-weight: normal;
  font-style: normal;
}

header.cd-auto-hide-header div.inner nav ul li:nth-child(1) a {
  padding-left: 0;
}

header.cd-auto-hide-header div.inner nav ul li:nth-child(4) a {
  padding-right: 0;
}

header.cd-auto-hide-header div.right {
  position: absolute;
  top: 30px;
  right: 10px;
  display: flex;
}

header.cd-auto-hide-header div.right div.search {
  margin-right: 30px;
}

header.cd-auto-hide-header div.right div.search a {
  display: inline-block;
}

header.cd-auto-hide-header div.right div.search a img {
  width: 100%;
  height: 100%;
}

header.cd-auto-hide-header div.right div.login a {
  display: inline-block;
}

header.cd-auto-hide-header div.right div.login a img {
  width: 100%;
  height: 100%;
}

@media all and (min-width:1921px) {
  header.cd-auto-hide-header div.inner nav ul li a {
    font-size: 22px;
  }
}

@media all and (min-width:1600px) and (max-width:1799px) {
  header.cd-auto-hide-header div.inner {
    width: 90%;
  }

  header.cd-auto-hide-header div.inner nav ul li a {
